<template>
    <div class="preview_wrapper">
        <div class="bg-img" v-if="!errorLoadImage" :style="`background-image: url(${previewData.bgUrl ?? previewData.url});`"></div>
        <!-- HEADER -->
        <div class="preview_header" :class="{'hide_handles': hideHandles}">
            <!-- NAME -->
            <div class="preview_image_name">
                <span :title="previewData.name">{{ previewData.name }}</span>
            </div>
    
            <!-- ZOOM -->
            <div class="preview_image_zoom" v-if="previewData.type === 'image'">
                <button class="zoomBtn" tabindex="-1" @click="handleZoom('decr', true)" :disabled="errorLoadImage || loadingFile">-</button>
                <span>{{Math.round(zoom * 100)}}%</span>
                <button class="zoomBtn" tabindex="-1" @click="handleZoom('incr', true)" :disabled="errorLoadImage || loadingFile">+</button>
            </div>

            <!-- RIGHT BUTTON -->
            <div class="top-buttons">
                <template v-if="clientWidth >= 768">
                    <button class="close button-style" @click="openFile(previewData)">
                        <img src="@/assets/images/open.png" alt="open" title="open in new tab" width="16px">
                    </button>
                    <button class="close button-style" style="margin-left: 15px;" @click="downloadFile(previewData?.url, `${previewData?.name}`)">
                        <img src="@/assets/images/download.png" alt="download" title="download" width="16px">
                    </button>
                    <button class="close button-style" style="margin-left: 15px;" @click="$emit('close')">
                        <img src="@/assets/images/close.png" alt="close" title="close" width="14px">
                    </button>
                </template>
                <template v-else>
                    <DropDown>
                        <template #button>
                            <button class="close button-style">
                                <img src="@/assets/images/dots.png" alt="options" title="actions" width="16px" style="filter: invert(1);">
                            </button>
                        </template>
                        <template #options>
                            <DropDownOption @click="openFile(previewData)">
                                <button class="close button-style mr-10px">
                                    <img src="@/assets/images/open.png" alt="open" title="open in new tab" width="16px">
                                </button>
                                Open in new tab
                            </DropDownOption>
                            <DropDownOption @click="downloadFile(previewData?.url, `${previewData?.name}`)" style="margin-bottom:0px !important;">
                                <button class="close button-style mr-10px">
                                    <img src="@/assets/images/download.png" alt="download" title="download" width="16px">
                                </button>
                                Download
                            </DropDownOption>
                        </template>
                    </DropDown>
                    <button class="close button-style" style="margin-left: 15px;" @click="$emit('close')">
                        <img src="@/assets/images/close.png" alt="close" title="close" width="14px">
                    </button>
                </template>
            </div>
        </div>

        <div class="preview_container" @dblclick="resetPosition()" @mousewheel="!errorLoadImage ? handleZoom($event) : null" v-show="!loadingFile">
            <!-- PREVIEW IMAGE -->
            <template v-if="!errorLoadImage">
                <!-- IMAGE -->
                <img
                    v-if="previewData.type === 'image'"
                    ref="imageRef"
                    :src="previewData.url"
                    @load="loadingFile = false, resetPosition()"
                    :alt="previewData.alt"
                    :title="previewData.title"
                    class="preview"
                    @error="errorLoad"

                    @mouseup.prevent="handleMouseUp"
                    @mousedown.prevent="handleMouseDown"
                    @mousemove.prevent="handleMouseMove"

                    @touchstart.prevent="handleTouchStart"
                    @touchmove.prevent="handleTouchMove"
                    @touchend.prevent="handleTouchEnd"
                >

                <!-- VIDEO -->
                <video v-else-if="previewData.type === 'video'" :title="previewData.title" class="preview" @error="errorLoad" controls @load="loadingFile = false">
                    <source :src="previewData.url" :alt="previewData.alt">
                    Your browser does not support the video tag.
                </video>

                <!-- OTHER FILES -->
                <img v-else :src="previewData.url" :alt="previewData.alt" @load="loadingFile = false" :title="previewData.title" class="preview" @error="errorLoad">
            </template>

            <!-- ON PREVIEW ERROR -->
            <template v-else>
                <div v-if="!defaultFailImage" class="no_preview_available">
                    <span>No preview available</span>
                </div>
                <img v-else :src="defaultFailImage" :alt="previewData.alt" :title="previewData.title" class="preview" @error="errorLoad">
            </template>
        </div>

        <!-- LOADING -->
        <div class="loading" v-if="loadingFile">
            <MacLoader/>
        </div>
    </div>
</template>

<script setup>
import { dragHelper, fileImageReplacer } from "@/components/organism/ImagePreviewer/helper";
import {defineProps, defineEmits, inject, ref, watch} from "vue";
import MacLoader from "../MacLoader/MacLoader.vue";
import DropDown from "@/components/molecules/DropDown/DropDown.vue";
import DropDownOption from "@/components/molecules/DropDownOption/DropDownOption.vue";

defineEmits(["close"]);

const clientWidth = inject("clientWidth");
const props = defineProps({
    image: {
        type: Object,
        required: true
    }
})

const {getFileImage} = fileImageReplacer();
const {handleMouseUp, handleMouseDown, handleMouseMove, handleTouchEnd, handleTouchStart, handleTouchMove} = dragHelper();

const defaultFailImage = inject("defaultFailImage")
const hideHandles = inject("hideHandles")
const errorLoadImage = ref(false);
const previewData = ref(props.image);
const defaultScale = 0.6;
const zoom = ref(defaultScale);
const loadingFile = ref(true);
const imageRef = ref(null);

watch(() => props.image, (val) => {
    previewData.value = {};
    if(val.type === "image") {
        zoom.value = defaultScale;
        loadingFile.value = true;
    } else {
        zoom.value = 1;
    }

    setTimeout(() => {
        if(val.type === "image" || val.type === "video") {
            previewData.value = JSON.parse(JSON.stringify(props.image));
        } else {
            previewData.value = {...props.image, url: getFileImage("")};
        }
        errorLoadImage.value = false;
    })
}, {immediate: true})

function handleZoom(e, manual = false) {
    if(props.image.type === "image") {
        let element;
        if(!manual) {
            element = e.target.classList.contains("preview") ? e.target : e.target.querySelector("img.preview");
        } else {
            element = document.querySelector(".preview_container img.preview");
        }
        const tmpZoom = Number(element.style.scale) || defaultScale;

        const scaleJump = 0.05;
        
        element.style.scale = ((!manual && e.deltaY > 0) || (manual && e === "decr")) ? (tmpZoom > scaleJump ? tmpZoom - scaleJump : scaleJump) : (tmpZoom < 5 ? tmpZoom + scaleJump : 5);

        zoom.value = element.style.scale;
    }
}

function resetPosition() {
    imageRef.value.style.position = "relative";
    imageRef.value.style.transform = "translate(0px, 0px)";
    if(props.image.type === "image") {
        imageRef.value.style.scale = defaultScale;
        zoom.value = defaultScale;
    } else {
        imageRef.value.style.scale = 1;
        zoom.value = 1;
    }
}

function openFile(file) {
    window.open(file.url, "_blank")
}

async function downloadFile(url, name) {
    const fileUrl = url;

    if(props.downloadFun) {
        props.downloadFun(fileUrl, name);
        return;
    }

    try {
        fetch(fileUrl)
        .then(response => response.blob())
        .then(blob => {
            const url = window.URL.createObjectURL(new Blob([blob]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', name);
            document.body.appendChild(link);
            link.click();
            link.remove();
        }).catch((error) => {
            console.error("ERROR in fetch resource: ", error);
        })

    } catch (error) {
        console.error('Error downloading the file:', error.message);
    }
}

function errorLoad() {
    errorLoadImage.value = true;
    loadingFile.value = false;
}
</script>

<style lang="css" scoped>
@import url("./style.css");
</style>