<template>
    <Teleport to="#imageWrapper" v-if="renderComponent">
        <div class="slider_controller">
            <template v-if="Object.keys(previewImage?.item || {}).length">
                <!-- ARROWS -->
                <button class="left arrow-container button-style" :class="{'hide_handles': hideHandles}" v-if="previewImage.index !== 0" @click="selectPrevious()">
                    <img src="@/assets/images/arrow.png" alt="left_arrow" class="left_arrow">
                </button>
                <button class="right arrow-container button-style" :class="{'hide_handles': hideHandles}" v-if="previewImage.index !== (items.length - 1)" @click="selectNext()">
                    <img src="@/assets/images/arrow.png" alt="right_arrow" class="right_arrow">
                </button>

                <ImagePreview
                    :image="previewImage.item"
                    @close="closeSlider()"
                    @mousedown="(e) => manageHandles(true)"
                    @mouseup="(e) => manageHandles(false)"

                    @touchstart="(e) => manageHandles(true)"
                    @touchend="(e) => manageHandles(false)"
                />
            </template>

            <div
                class="images_container scroll-smooth"
                :class="{'hide_handles': hideHandles}"
                id="CONTAINER"
                @mousedown="mouseDown"
                @mouseleave="mouseLeave"
                @mouseup="mouseUp"
                @mousemove="mouseMove"

                v-if="items.length > 1"
            >
                <img src="@/assets/images/arrow.png" alt="" class="arrow_image">
                <div class="filler-div"></div>

                <!-- IMAGES -->
                <template
                    v-for="(item, index) in (items)"
                    :key="item?.key || index"
                >
                    <ImageItem
                        :image="item"
                        :selected="previewImage?.index === index"
                        :index="index"
                        @click="selectImage(item?.key, index)"
                    />
                </template>

                <div class="filler-div"></div>
            </div>
        </div>
    </Teleport>
</template>

<script setup>
import { defineComponent, defineProps, onBeforeUnmount, onMounted, ref, defineEmits, computed, provide, nextTick, watch } from "vue";
import ImagePreview from "@/components/atom/ImagePreview/ImagePreview.vue";
import {dragScroll, fileImageReplacer, imagesContainerAdjust} from "./helper"
import ImageItem from "@/components/atom/ImageItem/ImageItem.vue";

const props = defineProps({
    items: {
        type: Array,
        required: true
    },
    defaultImage: {
        type: String,
        required: false
    },
    fileIcons: {
        type: Array,
        required: false
    },
    activeIndex: {
        type: Number,
        required: false
    },
    downloadFun: {
        type: Function,
        required: false
    },
    config: {
        type: Object,
        required: false
    }
})

const {initSlider, mouseDown, mouseLeave, mouseUp, mouseMove} = dragScroll();
const {checkPosition} = imagesContainerAdjust();
const {setFileImage} = fileImageReplacer()

provide("clientWidth", window.innerWidth);
const renderComponent = ref(false);

defineComponent({
    name: "Slider_Component"
})

const compoConfig = ref();
watch(() => props.config, (val) => {
    compoConfig.value = val;
}, {immediate: true})
provide("compoConfig", compoConfig)

const defaultFailImage = computed(() => props.defaultImage);
provide("defaultFailImage", defaultFailImage)
const emit = defineEmits(["close"])

const previewImage = ref();
const hideHandles = ref(false);
provide("hideHandles", hideHandles)

onMounted(() => {
    if(!document.getElementById("imageWrapper")) {
        const element = document.createElement("div")
        element.id = "imageWrapper"
        document.body.append(element);

    }
    renderComponent.value = true;

    nextTick(() => {
        hideHandlesFun();
        previewImage.value = {item: props.items[0], index: 0};
        startKeyListener();
        initSlider("CONTAINER");

        if(props.fileIcons?.length) {
            setFileImage(props.fileIcons);
        } else {
            setFileImage();
        }

        if(props.activeIndex && typeof props.activeIndex === "number") {
            selectImage(null, props.activeIndex)
            checkPosition({
                direction:"right",
                containerId: "CONTAINER",
                previewImage
            });
        }
    })
})

function startKeyListener() {
    document.addEventListener('keydown', keyListener)
    if(window.innerWidth >= 768) {
        document.addEventListener('mousemove', movementListener)
    }
}
function stopKeyListener() {
    document.removeEventListener('keydown', keyListener)
    if(window.innerWidth >= 768) {
        document.removeEventListener('mousemove', movementListener)
    }
}
function keyListener(e) {
    hideHandles.value = false;
    if(e.code === "Escape") {
        closeSlider();
    } else if(e.key.includes("Arrow")) {
        if(e.key.includes("Right"))
            selectNext()
        else if(e.key.includes("Left"))
            selectPrevious()
    }
}
function movementListener() {
    // hide handles if no movement for 3 seconds
    if(hideHandles.value) {
        hideHandles.value = false;
    }
    hideHandlesFun();
}

const hideHandlesFun = debounce(() => {
    if(window.innerWidth <= 768) return;

    setTimeout(() => {
        // hideHandles.value = false;
    }, (compoConfig.value.handlesTimer-1000))
}, 1000)


function debounce(func, timeout) {
    let timer;
    return function() {
        clearTimeout(timer);
        timer = setTimeout(func, timeout);
    }
}

function manageHandles(value) {
    hideHandles.value = value;
}

function selectNext() {
    if(previewImage.value.index < props.items?.length - 1) {
        const newIndex = previewImage.value.index + 1;
        const nextItem = props.items[newIndex]
        previewImage.value = {
            item: nextItem,
            index: newIndex
        }

        checkPosition({
            direction:"right",
            containerId: "CONTAINER",
            previewImage
        });
    }
}
function selectPrevious() {
    if(previewImage.value.index > 0) {
        const prevIndex = previewImage.value.index - 1;
        const prevItem = props.items[prevIndex]
        previewImage.value = {
            item: prevItem,
            index: prevIndex
        }

        checkPosition({
            direction:"left",
            containerId: "CONTAINER",
            previewImage
        });
    }
}

function closeSlider() {
    emit("close");
}

onBeforeUnmount(() => {
    stopKeyListener();
})

function selectImage(key, index) {
    const dir = index > previewImage.value.index ? "right" : "left";
    if(key) {
        const selectedIndex = props.items?.find((x) => x.key === key);

        if(selectedIndex !== -1) {
            previewImage.value = {item: props.items[selectedIndex], index};
        } else {
            previewImage.value = {item: props.items[index], index};
        }
    } else {
        previewImage.value = {item: props.items[index], index};
    }

    checkPosition({
        direction: dir,
        containerId: "CONTAINER",
        previewImage
    });
}
</script>

<style lang="css" scoped>
@import "./style.css"
</style>