<template>
  <div style="height: 100dvh; width: 100vw; overflow: auto;">
    <div class="gallery-preview" style="width: calc(100% - 20px); margin: 10px;">
      <div v-for="(item, index) in items" :key="index" class="gallery_file_container" @click="imageIndex = index, showImages = true">
        <img :src="item.url" :alt="item.alt" :title="item.title" style="width: 100%; height: 100%;">
      </div>
    </div>
    <ImagesPreviewer
      v-if="showImages"
      :items="items"
      :activeIndex="imageIndex"
      @close="closeSlider"
      :config="{ handlesTimer: 2000 }"
    />
  </div>
</template>

<script setup>
// import ImagesPreviewer from "image-previewer"
import ImagesPreviewer from "@/components/organism/ImagePreviewer/ImagesPreviewer.vue"
import { ref } from "vue"

import imagesArr from "./images.js";

const items = ref(imagesArr)

const showImages = ref(false)
const imageIndex = ref(0)
function closeSlider() {
  imageIndex.value = 0;
  showImages.value = false;
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
	margin: 0px;
}

button {
  cursor: pointer;
  border: none;
}


.gallery-preview {
  /* overflow-y: auto; */
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  column-gap: 8px;
  row-gap: 8px;
}

.gallery_file_container {
  min-height: 251px;
  border: 1px solid #cecece;
  border-radius: 8px;
  cursor: pointer;
  overflow: hidden;
  max-width: 250px;
}

.gallery-preview div {
  width: 100%;
}

.gallery-preview div img {
  object-fit: cover;
  height: 250px;
  cursor: pointer;
}

/* width */
::-webkit-scrollbar {
  width: 3px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}
</style>
