import { ref } from "vue";
import fileIconsArr from "./fileIcons.json"

const fileImages = ref([]);

export function dragScroll() {
    let isDown = false;
    let startX;
    let scrollLeft;
    let slider;

    function initSlider(id) {
        slider = document.querySelector(`#${id}`);
    }

    function mouseDown(e) {
        isDown = true;
        slider.classList.add('active');
        slider.classList.remove('scroll-smooth');
        startX = e.pageX - slider.offsetLeft;
        scrollLeft = slider.scrollLeft;
        event.target.style.cursor = 'grabbing';
    }
    function mouseUp() {
        isDown = false;
        slider.classList.remove('active');
        slider.classList.add('scroll-smooth');
        event.target.style.cursor = 'grab';
    }
    function mouseLeave() {
        isDown = false;
        slider.classList.remove('active');
        slider.classList.add('scroll-smooth');
    }
    function mouseMove(e) {
        if(!isDown) return;
        // e.preventDefault();
        const x = e.pageX - slider.offsetLeft;
        const walk = (x - startX); //scroll-fast
        slider.scrollLeft = scrollLeft - walk;
    }
    return {
        initSlider,
        mouseDown,
        mouseUp,
        mouseLeave,
        mouseMove
    }
}

export function imagesContainerAdjust() {
    function checkPosition({direction, containerId = "CONTAINER", previewImage}) {
        try {
            const container = document.getElementById(containerId)
            const newImage = document.getElementById(`image_${previewImage.value.index}`)?.getBoundingClientRect()

            const mid = container.clientWidth / 2;

            if(direction === "right") {
                let location = newImage.left + newImage.width
                if(location >= mid) {
                    const offset = location - (mid + (newImage.width / 2)) + 15;
                    container.scrollLeft = container.scrollLeft + offset;
                }
            } else {
                let location = newImage.right;
                if((location) <= mid) {
                    const offset = (mid + (newImage.width / 2)) - location - 15;
                    container.scrollLeft = container.scrollLeft - offset;
                }
            }
        } catch (error) {
            console.error("ERROR: ", error);
        }
    }

    return {
        checkPosition
    }
}

export function fileImageReplacer() {
    function setFileImage(icons=[]) {
        if(!icons?.length) {
            fileImages.value = fileIconsArr;
        } else {
            fileImages.value = icons
        }
    }

    function getFileImage(type) {
        const defaultFileImage = "";
        if(!type) {
            return defaultFileImage
        } else {
            return fileImages.value.find((x) => x.type.includes(type))?.url ?? defaultFileImage;
        }
    }

    return {
        getFileImage,
        setFileImage
    }
}

export function dragHelper() {
    let isDragging = false;
    let startX, startY, offsetX = 0, offsetY = 0;

    function handleMouseDown(event) {
        isDragging = true;
        startX = event.clientX - offsetX;
        startY = event.clientY - offsetY;
        event.target.style.cursor = 'grabbing';
    }

    function handleMouseMove(event) {
        if (!isDragging) return;

        offsetX = event.clientX - startX;
        offsetY = event.clientY - startY;
        event.target.style.transform = `translate(${offsetX}px, ${offsetY}px)`;
    }
    
    function handleMouseUp(event) {
        isDragging = false;
        event.target.style.cursor = 'grab';
    }

    function handleTouchStart(event) {
        isDragging = true;
        const touch = event.touches[0];
        startX = touch.clientX - offsetX;
        startY = touch.clientY - offsetY;
    }

    function handleTouchMove(event) {
        if (!isDragging) return;

        const touch = event.touches[0];
        offsetX = touch.clientX - startX;
        offsetY = touch.clientY - startY;
        event.target.style.transform = `translate(${offsetX}px, ${offsetY}px)`;
    }

    function handleTouchEnd() {
        isDragging = false;
    }
    return {
        handleMouseDown,
        handleMouseMove,
        handleMouseUp,
        handleTouchStart,
        handleTouchEnd,
        handleTouchMove
    }
}