<template>
    <div
        class="images"
        :class="{'selected': selected}"
        @click="$emit('click', $event)"
        draggable="false"
    >
        <!-- IMAGE -->
        <img
            :src="previewData.url"
            :alt="previewData.alt || ''"
            :id="`image_${index}`"
            loading="lazy"
            draggable="false"
        >

        <!-- NAME -->
        <span draggable="false" :title="`${previewData.name || ''}`" class="image-name">{{previewData.name || ''}}</span>
    </div>
</template>

<script setup>
import { fileImageReplacer } from "@/components/organism/ImagePreviewer/helper";
import {defineProps, ref, watch} from "vue";

const props = defineProps({
    image: {
        type: Object,
        required: true
    },
    index: {
        type: Number,
        required: true
    },
    selected: {
        type: Boolean,
        required: true
    }
})

const {getFileImage} = fileImageReplacer();

const previewData = ref(props.image)

watch(() => props.image, (val) => {
    if(val.type === "image") {
        previewData.value = JSON.parse(JSON.stringify(props.image));
    } else {
        previewData.value = {...props.image, url: getFileImage(val?.type)};
    }
}, {immediate: true})
</script>

<style lang="css" scoped>
@import url("./style.css");
</style>